@use '../base/variables' as v;
@use '../base/mixins' as m;

.crear{
    margin-top: 5rem;
    .uptask{
        @include m.textoDegradado(v.$cyan,v.$naranja);
    }
    
    input[type="submit"]{
        @include m.boton(v.$cyan,v.$blanco);
    }
    .acciones{

        a{
            color: v.$cyan;
        }
    }
}