@use '../base/variables' as v;
@use '../base/mixins' as m;

.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal{
        flex: 1;
        overflow: scroll;
    }

    .contenido{
        padding: 2rem;
        

        .nombre-pagina{
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario{
        input:not([type="submit"]){
            background-color: v.$blanco;
        }

        input[type="submit"]{
            @include m.boton(v.$indigo,v.$blanco);
            font-weight: normal;
            border-radius: .5rem;
        }
    }

    .enlace{
        display: inline-flex;
        align-items: center;
        color: v.$blanco;
        background-color: v.$cyan;
        padding: 1rem 2rem;

        &:hover{
            background-color: darken($color: v.$cyan, $amount: 5);
        }
    }
}