@use '../base/variables' as v;
@use '../base/mixins' as m;

.no-proyectos{

    a{
        color: v.$indigo;
    }
}

.listado-proyectos{
    list-style: none;
    padding: 0;
    margin-top: 3rem;    
    @include m.grid(1,2rem);
    @include m.tablet {
        @include m.grid(2,2rem);
    }
    @include m.desktop {
        @include m.grid(3,2rem);
    }

    .proyecto{
        display: flex;
        align-items: center;
        background-color: lighten($color: v.$indigo, $amount: 5);
        min-height: 12rem;    
        padding: 2rem;    
        line-height: 2;
        border-radius: .5rem;

        &:hover{
            background-color: darken($color: v.$indigo, $amount: 5);
        }

        a{
            color: v.$blanco;
        }
    }
}